.popup {
  position: fixed;
  z-index: 11;
  left: 0;
  top: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  padding: rem(15px);
  width: 100%;
  height: 100%;
  background-color: rgba(#000000, 0.8);

  &_close {
    display: none;
  }

  &__cross {
    position: absolute;
    top: rem(15px);
    right: rem(15px);
  }

  &__content {
    &_youtube-video {
      width: 100%;
      max-width: 576px;
      height: 315px;
    }
  }
}